@use "global" as *;
.page__header {
  position: relative;
  width: 100%;
  &::before {
    content: "";
    display: block;
    padding-top: 113%;
    @include mq("md") {
      padding-top: 29%;
    }
  }
}

.page__header-image {
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.page__header-title {
  font-size: rem(35);
  font-weight: $bold;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  letter-spacing: 2px;
}
